.graph-modal {
  background: rgb(109 146 152 / 46%);
  backdrop-filter: blur(50px);

  &__form {
    border-radius: 0;
    padding: 30px;
    width: 468px;

    @include mobile {
      width: 90%;
    }
  }

  &__info {
    border-radius: 0;
    padding: 30px;
    width: 914px;

    @include tablet {
      width: 90%;
    }
  }

  .graph-modal__close {
    right: 30px;
    top: 30px;
  }
}

.modal-form {
  display: flex;
  flex-direction: column;

  &__title {
    margin: 0;
    margin-bottom: 30px;
    font-size: 24px;
    line-height: 150%;
    color: var(--dark-color);
  }

  &__text {
    font-weight: 500;
    font-size: 20px;
    line-height: 150%;
    color: var(--dark-color);
  }

  &__input {
    margin-bottom: 20px;
    border: 1px solid #b1d2d7;
    padding: 11px 20px 12px;
    width: 100%;
    height: 45px;
    font-size: 17px;
    line-height: 130%;
    color: var(--dark-color);
    background-color: #fff !important;
    transition: 0.1s;

    &::placeholder {
      color: rgb(49 49 49 / 48%);
    }

    &:focus {
      outline: 2px solid var(--blue-color);
      outline-offset: 0;
    }

    &:hover {
      outline: 2px solid var(--blue-color);
      outline-offset: 0;
    }
  }

  &__textarea {
    margin-bottom: 40px;
    border: 1px solid #b1d2d7;
    padding: 11px 20px 12px;
    width: 100%;
    height: 120px;
    font-size: 17px;
    line-height: 130%;
    color: var(--dark-color);
    background-color: #fff !important;
    transition: 0.1s;
    resize: none;

    &::placeholder {
      color: rgb(49 49 49 / 48%);
    }

    &:focus {
      outline: 2px solid var(--blue-color);
      outline-offset: 0;
    }

    &:hover {
      outline: 2px solid var(--blue-color);
      outline-offset: 0;
    }
  }

  &__btn {
    display: inline-block;
    margin-bottom: 20px;
    padding: 12px 32px;
    font-weight: 500;
    font-size: 17px;
    line-height: 21px;
    text-transform: uppercase;
    color: var(--light-color);
    background-color: var(--blue-color);
  }

  &__personal {
    font-size: 14px;
    line-height: 130%;
    color: rgb(49 49 49 / 60%);
  }
}

.modal-info {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;

  picture {
    flex: 1 0 auto;
    margin-right: 30px;

    @include tablet {
      margin-bottom: 30px;
    }
  }

  &__descr {
    border-left: 1px solid var(--lightdark-color);
    padding-left: 30px;

    @include tablet {
      border-top: 1px solid var(--lightdark-color);
      border-left: none;
      padding-top: 30px;
      padding-left: 0;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 10px;
    font-weight: 500;
    font-size: 20px;
    line-height: 130%;
    text-transform: uppercase;
    color: var(--dark-color);
  }

  &__resident {
    display: inline-block;
    margin-bottom: 20px;
    font-size: 15px;
    line-height: 130%;
    color: var(--grey-color);
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 17px;
    line-height: 150%;
    color: var(--dark-color);
  }

  @include tablet {
    flex-direction: column;
  }
}
